import Cell from '../../../components/Cell/Cell.vue'

import { computedRequired } from '../../../utils/utils.js'
import JobModel from '@/models/job.js'
const jobModel = new JobModel()
export default {
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			id: '',
			jobInfo:{}
		}
	},
	onLoad(option){
		this.id = option.id
		this.getJobDetail()
	},
	components: {
		Cell
	},
	filters: {
		filterProbation(data){
			if(data.probation === 0){
				return '无试用期'
			}
			return `时间：${data.trialTime} | 薪资: ${data.trialSalary}`
		}
	},
	computed: {
		required(){
			return computedRequired(this.jobInfo)
		}
	},
	methods: {
		getJobDetail(){
			uni.showLoading({
				mask: true
			})
			jobModel.jobDetail(this.id).then(res=>{
				uni.hideLoading()
				const { code, data, message } = res.data
				if(code === '0'){
					this.jobInfo = data
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '获取岗位详情失败'
				})
			})
		}
	}
}
